/*BLOCKER*/



#blocker {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: transparent;
  top: 0px;
  display: block;
  pointer-events: all;
  z-index: 10;
  flex-direction: column;
  transition: all 0.3s;

  .body {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: baseline;
    flex-direction: column;
    /*padding: 2vh;*/
    margin-left: 15vh;
    transition: all 1s;

    #settings-menu-title {
      background-color: rgba(128, 128, 128, 0.5);
      filter: none !important;
      padding: 0px !important;

      height: 6vw !important;
      top: -6vw;
      width: auto !important;
      display: none;
      justify-content: center;
      align-items: center;
      /*font-size: 2vw;*/

      font-weight: 900;
      font-size: 4vh;

      pointer-events: none;
      padding-left: 20px !important;
      padding-right: 20px !important;
    }

    .settings-menu {
      background: rgba(128, 128, 128, 0.5);
      width: 80%;
      height: auto;
      display: flex;
      flex-direction: column;
      font-size: 1.5vw;
      justify-content: flex-start;
      align-items: flex-start;
      text-align: left;
      font-weight: bolder;
      transition: all 0.3s;

      max-height: 60%;
      overflow-y: scroll;

      #options-settings,
      #options-single,
      #options-community,
      #options-about,
      #options-patreon {
        width: 100%;
        display: none;
      }

      #options-about,
      #options-patreon {
        pointer-events: none;

        span {
          display: block;
          width: 95%;
          text-align: justify;
        }
      }

      #options-main {
        width: 100%;
        display: none;
      }

      #options-video,
      #options-controls,
      #options-audio {
        width: 100%;
        display: none;

        .options-row {
          width: 100%;
          display: flex;

          .option-name {
            width: 60%;
          }

          .option-value {
            width: 40%;
            display: flex;
            align-items: center;
            justify-content: end;

            select {
              width: 80%;
              height: 50%;
              outline: none;
              margin-right: 10%;
            }

            input[type="checkbox"] {
              width: 30%;
              height: 30%;
            }

            input[type="range"] {
              width: 65%;
              height: 30%;
            }

            input[type="number"] {
              width: 15%;
              height: 30%;
              margin-right: 4vh;
              font-size: 2vh;
              border: none;
              pointer-events: none;
            }
          }
        }
      }

      span {
        width: calc(100% - 20px);
        height: auto;
        padding-top: 15px;
        padding-bottom: 15px;
        display: flex;
        align-items: center;
        cursor: pointer;
        padding-left: 20px;
        transition: all 0.1s;
        font-size: 3vh;
      }

      span:hover {
        background: #d1bb53;
        filter: drop-shadow(1 2px 3px black);
        color: black;
      }

      .sub-option {
        display: none;
      }
    }

    #settings-menu {
      display: flex;

      button {
        width: 12vh;
        height: 4vh;
        border: none;
        background: rgb(40, 40, 40);
        color: white;
        font-weight: bolder;
        margin-top: 2vh;
        filter: drop-shadow(2px 4px 6px black);
        font-size: 2vh;
        display: none;
      }

      button:hover {
        background: #d1bb53;
        filter: drop-shadow(1 2px 3px black);
        color: black;
      }

      #done {
        margin-left: 3vh;
      }
    }

  }
}

:root {
  --arrow-bg: rgba(255, 255, 255, 0.3);
  --arrow-icon: url(https://upload.wikimedia.org/wikipedia/commons/9/9d/Caret_down_font_awesome_whitevariation.svg);
  --option-bg: white;
  --select-bg: rgba(255, 255, 255, 0.2);
}

/* <select> styles */
select {
  /* Reset */
  appearance: none;
  border: 0;
  outline: 0;
  font: inherit;
  /* Personalize */
  width: 20rem;
  padding-left: 10px;
  font-size: 2.5vh;
  background: var(--arrow-icon) no-repeat right 0.8em center / 1.4em,
    linear-gradient(to left, var(--arrow-bg) 3em, var(--select-bg) 3em);
  color: white;
  border-radius: 0.25em;
  box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;

  /* Remove IE arrow */
  &::-ms-expand {
    display: none;
  }

  /* Remove focus outline */
  &:focus {
    outline: none;
  }

  /* <option> colors */
  option {
    color: inherit;
    background-color: grey;
  }
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: auto;
  scrollbar-color: rgba(255, 255, 255, 0.4);
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 16px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 0px;
}

