#list-custm-chambers,
#list-main-chambers {
  display: grid;
  grid-template-columns: auto auto;
  margin-top: 70px;
  overflow: hidden;

  #options-custom {
    margin-top: -70px;
    position: fixed;
    width: auto;
    display: flex;
    overflow-x: hidden;
    z-index: 10000;
    background-color: #999;

    span {
      width: auto;
      padding: 15px;
    }

    #option-cutom-all {
      background-color: grey;
    }
  }

  .custom-chamber {
    width: 90%;
    height: 20vw;
    margin: 3px;
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
    transition: all 0.5s;
    border: 5px solid black;
    overflow: hidden;

    .delete-my-chamber{
      background-color: red;
      border: none;
      padding: 10px;
      position: absolute;
      top: 0px;
      right: 0px;
      cursor: pointer;
      transition: all 0.2s;
      font-weight: bolder;
      color: white;
    }

    .delete-my-chamber:hover{
      transform: scale(1.1);
      color: black;
    }

    .chamber-stats{
      position: absolute;
      top: 0px;
      left: 0px;
      width: auto;
      height: auto;
      display: flex;
      flex-direction: column;
      background-color: rgba(0, 0, 0, 0.5);
      pointer-events: none;
      padding-right: 40px;

      span{
        font-size: 15px;
      }
    }

    .span-chamber {
      position: absolute;
      bottom: 0px;
      background-color: rgba(0, 0, 0, 0.75);
    }

    .fa-thumbs-up {
      right: 10px;
      top: 10px;
      position: absolute;
      transition: all 0.5s;
    }

    .fa-thumbs-down {
      right: 60px;
      top: 10px;
      position: absolute;
      transition: all 0.5s;
    }

    .fa-thumbs-up:hover {
      color: green;
    }

    .fa-thumbs-down:hover {
      color: red;
    }
  }

  .custom-chamber:hover {
    filter: drop-shadow(2px 4px 6px black);
    border: 5px solid orange;
  }
}

