.loading {
  position: absolute;
  bottom: 100px;
  right: 20px;
  margin: -36px 0 0 -36px;
  height: 40px;
  width: 40px;
  border: 8px dotted #ffffff;
  border-radius: 100%;
  animation: loading 2s infinite linear;
  z-index: 10;
}

#container {
  width: 100%;
  height: 100%;
  transition: all 1s;
}

#container canvas {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 1;
}

#container img {
  pointer-events: none;
}

.hero {
  color: #fff;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  display: block;
  width: 100%;
  padding-left: clamp(10px, 5vw, 4em);
  padding-right: clamp(10px, 5vw, 4em);
  position: absolute;
  bottom: 150px;
  z-index: 10;
}

.main-title {
  color: #fff;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  font-size: 1.3em;
  margin-top: clamp(1em, 8.5vh, 10em);
}

.main-title span {
  color: #fff;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  font-size: 3em;
  display: block;
}

.introduction-text {
  color: #fff;
  box-sizing: border-box;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  max-width: 700px;
  padding-left: 1.5em;
  border-left: 2px solid #fff;
  margin: 2em 0;
  line-height: 1.5;
}

.loading-bar {
  bottom: 120px;
  right: 20px;
  position: absolute;
  margin: 0 auto;
  height: 25px;
  width: 30%;
  border-radius: 50px;
}

.loading-bar span {
  display: block;
  position: relative;
  height: 100%;
  width: 100%;

  border-radius: 50px;
  background-image: linear-gradient(to bottom, #bebebe, #555353 60%);
  box-shadow:
    inset 0 2px 9px rgba(255, 255, 255, 0.3),
    inset 0 -2px 6px rgba(0, 0, 0, 0.4);
  overflow: hidden;
  animation: loading 2s infinite linear;
}

.loading-bar span:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(-45deg,
      rgba(255, 255, 255, .2) 25%,
      transparent 25%,
      transparent 50%,
      rgba(255, 255, 255, .2) 50%,
      rgba(255, 255, 255, .2) 75%,
      transparent 75%,
      transparent);
  z-index: 1;
  background-size: 50px 50px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  overflow: hidden;
}

@keyframes load {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 50px 50px;
  }
}

.image {
  position: absolute;
  width: 100%;
  opacity: 0;
  animation: change-img-anim 15s infinite;
}

.image:nth-of-type(1) {
  animation-delay: 0s;
}

.image:nth-of-type(2) {
  animation-delay: 5s;
}

.image:nth-of-type(3) {
  animation-delay: 10s;
}

@keyframes change-img-anim {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

/* Menu */

.menu {
  position: absolute;
  width: 250px;
  padding: 2px;
  margin: 0;
  border: 1px solid #bbb;
  background: #eee;
  background: linear-gradient(to bottom, #fff 0%, #e5e5e5 100px, #e5e5e5 100%);
  z-index: 10000000;
  border-radius: 3px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, .2);
  opacity: 0;
  transform: translate(0, 15px) scale(.95);
  transition: transform 0.1s ease-out, opacity 0.1s ease-out;
  pointer-events: none;
  display: none;
}

.menu-item {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  white-space: nowrap;
}

.menu-btn {
  display: block;
  color: #444;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  cursor: pointer;
  border: 1px solid transparent;
  white-space: nowrap;
  padding: 6px 8px;
  border-radius: 3px;
}

button.menu-btn {
  background: none;
  line-height: normal;
  overflow: visible;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  width: 100%;
  text-align: left;
}

a.menu-btn {
  outline: 0 none;
  text-decoration: none;
}

.menu-text {
  margin-left: 25px;
}

.menu-btn .fa {
  position: absolute;
  left: 8px;
  top: 50%;
  transform: translateY(-50%);
}

.menu-item:hover>.menu-btn {
  color: #fff;
  outline: none;
  background-color: #2E3940;
  background: linear-gradient(to bottom, #5D6D79, #2E3940);
  border: 1px solid #2E3940;
}

.menu-item-disabled {
  opacity: .5;
  pointer-events: none;
}

.menu-item-disabled .menu-btn {
  cursor: default;
}

.menu-separator {
  display: block;
  margin: 7px 5px;
  height: 1px;
  border-bottom: 1px solid #fff;
  background-color: #aaa;
}

.menu-item-submenu::after {
  content: "";
  position: absolute;
  right: 6px;
  top: 50%;
  transform: translateY(-50%);
  border: 5px solid transparent;
  border-left-color: #808080;
}

.menu-item-submenu:hover::after {
  border-left-color: #fff;
}

.menu .menu {
  top: 4px;
  left: 99%;
}

.menu-show,
.menu-item:hover>.menu {
  opacity: 1;
  transform: translate(0, 0) scale(1);
  pointer-events: auto;
  display: block;
}

.menu-item:hover>.menu {
  transition-delay: 300ms;
}

#settings-menu-grid {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  display: flex;
  flex-wrap: wrap;
}

#settings-menu-grid div {
  width: 19.5%;
